.controlPane {
  padding: 24px;
  /* Optical centering */
  padding-top: 18px;
  background-color: white;
  border-radius: 8px;
  /*
    HACK: Margin on an outer bound like this is
    a little gross, but we haven't seen the tools
    to deal with it yet!
  */
  margin-bottom: 32px;
  position: relative;
}

.title {
  margin: 0;
  /* Optical centering */
  margin-bottom: -4px;
}

.metadata {
  margin-left: 16px;
  font-size: 0.875rem;
  font-weight: 400;
  color: hsl(0deg, 0%, 30%);
}

@media screen and (max-width: 500px) {
  .controlPane {
    padding: 16px;
    margin-left: 16px;
    scroll-padding: 16px;
    /* border: 1px solid gold; */
  }
  .title {
    padding-bottom: 16px;
  }
}
